'use client';

import React, { useState, useEffect } from 'react';
import Link from 'next/link';
import { ChevronLeft, ChevronRight, Home, ListTodo, Timer, Calendar, Settings, Star, FolderClosed } from 'lucide-react';
import { cn } from '@/lib/utils';

interface SidebarItem {
  icon: React.ReactNode;
  label: string;
  href: string;
}

interface SidebarProps {
  className?: string;
  onCollapsedChange?: (collapsed: boolean) => void;
  defaultCollapsed?: boolean;
}

export function Sidebar({ className, onCollapsedChange, defaultCollapsed = false }: SidebarProps) {
  const [collapsed, setCollapsed] = useState(defaultCollapsed);
  
  // Update parent component when collapsed state changes
  useEffect(() => {
    if (onCollapsedChange) {
      onCollapsedChange(collapsed);
    }
  }, [collapsed, onCollapsedChange]);
  
  const toggleSidebar = () => {
    setCollapsed(!collapsed);
  };

  const menuItems: SidebarItem[] = [
    { icon: <Home size={20} />, label: 'Dashboard', href: '/dashboard' },
    { icon: <ListTodo size={20} />, label: 'Tasks', href: '/tasks' },
    { icon: <Timer size={20} />, label: 'Pomodoro', href: '/pomodoro' },
    { icon: <Calendar size={20} />, label: 'Calendar', href: '/calendar' },
    { icon: <Star size={20} />, label: 'Favorites', href: '/favorites' },
    { icon: <FolderClosed size={20} />, label: 'Projects', href: '/projects' },
    { icon: <Settings size={20} />, label: 'Settings', href: '/settings' },
  ];

  return (
    <aside 
      className={cn(
        'fixed left-0 top-16 h-[calc(100vh-4rem)] bg-background border-r border-border transition-all duration-300 ease-in-out z-40',
        collapsed ? 'w-16' : 'w-64',
        className
      )}
    >
      <div className="flex flex-col h-full">
        <div className="flex items-center justify-end p-2 border-b border-border">
          <button 
            onClick={toggleSidebar}
            className="p-2 rounded-md hover:bg-accent text-muted-foreground hover:text-foreground transition-colors"
            aria-label={collapsed ? "Expand sidebar" : "Collapse sidebar"}
          >
            {collapsed ? <ChevronRight size={18} /> : <ChevronLeft size={18} />}
          </button>
        </div>
        
        <nav className="flex-1 overflow-y-auto py-4">
          <ul className="space-y-1 px-2">
            {menuItems.map((item) => (
              <li key={item.href}>
                <Link
                  href={item.href}
                  className={cn(
                    'flex items-center rounded-md p-2 text-foreground hover:bg-accent hover:text-accent-foreground transition-colors',
                    collapsed ? 'justify-center' : 'px-3'
                  )}
                >
                  <span className="flex-shrink-0">{item.icon}</span>
                  {!collapsed && (
                    <span className="ml-3 text-sm font-medium transition-opacity duration-300">
                      {item.label}
                    </span>
                  )}
                </Link>
              </li>
            ))}
          </ul>
        </nav>
      </div>
    </aside>
  );
} 